html,
body {
	font-size: 10px;
	font-family: sans-serif;
	background: #e5e5e5;
}

p {
	font-size: 1.6rem;
	line-height: 1.5;
}

h1 {
	font-family: billabong, "billabongregular";
	text-align: center;
	font-weight: 100;
	font-size: 13rem;
	margin: 2rem 0;
	letter-spacing: -1px;
	color: black;
	text-decoration: none;
}

h1 a {
	color: black;
	text-decoration: none;
}

h1 a:focus {
	outline: 0;
}

@font-face {
	font-family: "billabongregular";
	src: url("https://cdn.rawgit.com/milktronics/beaglegr.am/master/public/fonts/billabong-webfont.eot");
	src: url("https://cdn.rawgit.com/milktronics/beaglegr.am/master/public/fonts/billabong-webfont.eot?#iefix")
			format("embedded-opentype"),
		url("https://cdn.rawgit.com/milktronics/beaglegr.am/master/public/fonts/billabong-webfont.woff")
			format("woff"),
		url("https://cdn.rawgit.com/milktronics/beaglegr.am/master/public/fonts/billabong-webfont.ttf")
			format("truetype"),
		url("https://cdn.rawgit.com/milktronics/beaglegr.am/master/public/fonts/billabong-webfont.svg#billabongregular")
			format("svg");
	font-weight: normal;
	font-style: normal;
}

.huge {
	padding: 1em;
	color: #;
}

.photoGrid {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	max-width: 1000px;
	margin: 0 auto;
}

.figure {
	-ms-flex-preferred-size: calc(33.333% - 4rem);
	    flex-basis: calc(33.333% - 4rem);
	border: 1px solid #d3d3d3;
	padding: 2rem;
	-ms-flex-positive: 1;
	    flex-grow: 1;
	margin: 0 2rem 2rem 2rem;
}

.figure .single-photo {
	-ms-flex-preferred-size: calc(33.333% - 4rem);
	    flex-basis: calc(33.333% - 4rem);
	-ms-flex-positive: 1;
	    flex-grow: 1;
	-ms-flex-negative: 0;
	    flex-shrink: 0;
	margin: 0 2rem 2rem 2rem;
	padding: 2rem;
	border: 1px solid #e5e5e5;
	background: #fff;
	position: relative;
}

.single-photo {
	max-width: 1000px;
	margin: 0 auto;
	display: -ms-flexbox;
	display: flex;
	background: #e5e5e5;
	border: black;
}

.single-photo .figure {
	-webkit-box-shadow: none;
	        box-shadow: none;
	margin: 0 2rem 0 0;
	border: 0;
	padding: 0;
	-ms-flex: 1 0 60%;
	    flex: 1 0 60%;
	max-width: 60%;
}

.single-photo .comments {
	-ms-flex: 1 0 40%;
	    flex: 1 0 40%;
	max-width: 40%;
}

.single-photo .photo {
	width: 100%;
	margin: 0;
}

.photo {
	width: calc(100% + 4em);
	height: calc(100% - 5em);
	margin-left: -2rem;
	margin-top: -2rem;
	max-width: none;
}

.comment {
	width: 100%;
}
.comment p {
	border-bottom: 1px solid #0000ff;
	padding: 0.5rem 2rem;
}
.comment-form input,
.comment-form textarea {
	width: 100%;
	border: 0;
	font-size: 1.8rem;
	padding: 1rem 1rem;
	border-bottom: 1px solid #d3d3d3;
	outline: none;
	resize: vertical;
	background-color: #d3d3d3;
}

.addIcon {
	background: url("https://image.flaticon.com/icons/svg/60/60740.svg") center
		no-repeat;
	background-size: contain;
	padding: 40px;
	display: table;
	margin: 20px auto;
	border: none;
}

.button-container {
	margin: 0 auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
}

button,
.button {
	border: 2px solid #f2f2f2;
	background: none;
	-ms-flex-preferred-size: 48%;
	    flex-basis: 48%;
	display: inline-block;
	line-height: 2;
	text-decoration: none;
	padding: 5px;
	text-align: center;
	font-size: 15px;
	color: red;
	-webkit-transition: all 0.2s;
	-o-transition: all 0.2s;
	transition: all 0.2s;
	-webkit-box-sizing: padding-box;
	        box-sizing: padding-box;
}

button:hover,
.button:hover,
button:focus,
.button:focus {
	border-color: #00f;
	outline: 0;
}

.speech-bubble {
	background: url("https://d30y9cdsu7xlg0.cloudfront.net/png/51904-200.png")
		center no-repeat;
	background-size: contain;
	padding: 10px;
	margin: 5px;
	display: inline-block;
	border: none;
	position: relative;
}

.comment-count {
	margin: 0 auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	color: black;
}

.form {
	width: 360px;
	padding: 0 0 0;
	margin: auto;
}

.form input {
	font-family: sans-serif;
	outline: 0;
	background: #f2f2f2;
	width: 100%;
	border: 0;
	margin: 0 0 15px;
	padding: 15px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	font-size: 14px;
}

.form button {
	font-family: "Roboto", sans-serif;
	text-transform: uppercase;
	outline: 0;
	background: #065784;
	width: 100%;
	border: 0;
	padding: 15px;
	color: #ffffff;
	font-size: 14px;
	-webkit-transition: all 0.3 ease;
	-o-transition: all 0.3 ease;
	transition: all 0.3 ease;
	cursor: pointer;
}

.form button:hover,
.form button:active,
.form button:focus {
	background: #7b8589;
}

.loader {
	height: 20%;
	width: 100%;
	display: -ms-flexbox;
	display: flex;
	position: fixed;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	font-size: 28pt;
}



